import React, { useState } from 'react';
import {
  Navbar,
  NavbarBrand,
  NavbarToggler,
  Nav,
  NavItem,
  NavLink,
  Collapse, UncontrolledDropdown, DropdownToggle, DropdownMenu,
} from 'reactstrap'
import { graphql, StaticQuery } from 'gatsby'
import DropdownItem from 'reactstrap/es/DropdownItem'

const Header = ({siteTitle, showTitle}) => {

  const [isOpen, setOpen] = useState(false);

  return (
    <StaticQuery  query={graphql`
    {
        categories: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}, exclude: {ne: true}}}) {
            edges {
                node {
                    frontmatter {
                        tag
                        name
                        icon
                    }
                    fields {
                        slug
                    }
                }
            }
        }
        topLists: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}, exclude: {eq: true}}}) {
            edges {
                node {
                    frontmatter {
                        tag
                        name
                        icon
                    }
                    fields {
                        slug
                    }
                }
            }
        }
        areas: allMarkdownRemark(filter: {frontmatter: {type: {eq: "area"}}}, sort: {fields: [frontmatter___name]}) {
            edges {
              node {
                fields {
                  slug
                }
                frontmatter {
                  name
                  tag
                }
              }
            }
          }
        }

    `}
      render={data => {
        const categories = data.categories.edges.map(edge => edge.node);
        const areas = data.areas.edges.map(edge => edge.node);
        const topLists = data.topLists.edges.map(edge => edge.node);

        return (
        <header>
          <Navbar expand="md" className="navbar-dark bg-transparent">
            {showTitle && <NavbarBrand href="/"><h1 className="title--header brand">{siteTitle}</h1></NavbarBrand>}
            <NavbarToggler className="link light" onClick={() => setOpen(!isOpen)}/>
            <Collapse isOpen={isOpen} navbar>
              <Nav className="ml-auto" navbar>
                <NavItem>
                  <NavLink className="link light" href="/our-story/">Our Story</NavLink>
                </NavItem>

                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav caret className="link light">
                    Areas
                  </DropdownToggle>
                  <DropdownMenu right>
                    {
                      areas.map((area, i) => (
                        <DropdownItem key={i} href={ area.fields.slug }>{ area.frontmatter.name }</DropdownItem>
                      ))
                    }
                  </DropdownMenu>
                </UncontrolledDropdown>

                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav caret className="link light">
                    Categories
                  </DropdownToggle>
                  <DropdownMenu right>
                    {
                      categories.map((cat, i) => (
                        <DropdownItem key={i} href={ cat.fields.slug }>{ cat.frontmatter.name }</DropdownItem>
                      ))
                    }
                  </DropdownMenu>
                </UncontrolledDropdown>

                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav caret className="link light">
                    Top Things To Do
                  </DropdownToggle>
                  <DropdownMenu right>
                    {
                      topLists.map((top, i) => (
                        <DropdownItem key={i} href={ top.fields.slug }>{ top.frontmatter.name }</DropdownItem>
                      ))
                    }
                  </DropdownMenu>
                </UncontrolledDropdown>

                <NavItem>
                  <NavLink className="link light" href="/blog/">Blog</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink className="link light" href="/contact/">Free business listing</NavLink>
                </NavItem>
              </Nav>
            </Collapse>
          </Navbar>
        </header>
      );
    }
    }/>

  );
};

export default Header
